export default {
  name: 'CgmFloatActMenu',
  props: {
    defaultkey: {
      type: String,
      default: 'base'
    },
    menus: {
      type: Array,
      default: () => [{
        key: 'basic',
        title: '基本',
        icon: ''
      }]
    }
  },
  data () {
    return {
      fabActive: false,
      appeared: ''
    }
  },
  mounted () {
    this.appeared = this.$props.defaultkey
  },
  methods: {
    setAppeared (key) {
      this.appeared = key
    }
  }
}
