export default {
  name: 'CgoUserBrandPermmit',
  props: {
    bid: {
      type: String,
      default: ''
    },
    need: {
      type: String,
      default: 'MNG'
    }
  },
  computed: {
    hasPermit () {
      return this.$store.getters['xd/general/xdgcuser/hasBrandPermit'](this.$props.bid, this.$props.need)
    }
  }
}
